<template>
	<div class="mingyan">
		<div class="headeranimate" :style="{width: maxQuotes * 5 + '%'}"></div>
		<p v-if="maxQuotes > 0">{{maxQuotes}} / 20</p>
		<newQuote :maxQuotes="maxQuotes" @quoteAdd="newquote" @close="close" v-show="newquotebox"></newQuote>
		<quoteGrid @quoteDelete="dequote" :quotes='quotes'></quoteGrid>
		<div class="add" @click="addquote()">
			<img src="../assets/images/add.png">
		</div>
	</div>
</template>

<script>
	import quoteGrid from '../components/mingyan/quoteGrid.vue'
	import newQuote from '../components/mingyan/newQuote.vue'


	export default{
		data(){
			return {
				quotes:[
					'在民主国家，你的投票很重要。在封建国家，你的地位很重要',
					'你对政治不感兴趣，并不意味着政治对你不感兴趣',
					'不要盯着大钟看，要做大钟正在做的事。继续前进',
					'我喜欢猪。狗崇拜人类。猫鄙视人类。猪对我们一视同仁',
					'大船可以多冒险，而小船则不能离岸太远',
					'我不相信星座。我是射手座天生多疑',
					'我无法想象出人类是在什么情况下造出了船…现代造船已不止于此',
					'在水手和来世之间除了块厚木板，什么也没有',
					'前路有危险？我并不会避开它们。最危险的地方就是最安全的地方',
					'故上兵伐谋，其次伐交，其次伐兵，其下攻城。攻城之法为不得已',
					'一旦尝过飞行的滋味，便会永远仰望天空，因为你曾去过那里，并且渴望回到那里',
					'在理性的子弹面前，无知的勇气是没用的',
					'沃森先生…过来一下…我想见你',
					'民族主义促使国家产生，而不是国家产生了民族主义',
					'如果你不看报纸，你会很无知。如果你看报纸，你会被误导',
					'为了团结人民，政府需要敌人…如果没有真正的敌人，他们会造一个，以此让大家动员起来',
					'资本主义的固有邪恶是幸福的不平等分享；社会主义的固有邪恶是痛苦的平等分享',
					'一个对历史、起源和文化不了解的人，就好比没有根的大树',
					'亲近你的朋友，但更要亲近你的敌人'
					],
				maxQuotes: 19,
				newquotebox:false,
			}
		},
		components:{
			quoteGrid,
			newQuote
		},
		methods:{
			newquote(quote){
				this.quotes.push(quote)
				this.maxQuotes ++
			},
			dequote(index){
				this.quotes.splice(index, 1)
				this.maxQuotes --
			},
			addquote(){
				this.newquotebox = true
			},
			close(){
				this.newquotebox = false
			}
		}

	}
</script>


<style lang="scss" scoped> 
	.add{
		position: absolute;
		right: 1%;
		bottom: 1.1rem;
		width: .8rem;
		height: .8rem;
		background:green;
		border-radius: 50%;
		img{
			width: .6rem;
			height: .6rem;
			position:absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom:0;
			margin: auto;
		}
	}
	.headeranimate{
		width: 80%;
		height: 3px;
		background:green;
		position: absolute;
		top: 0;
		left: 0;
		transition: all .5s;	
	}
	p{
		position:absolute;
		width: 100%;
		text-align: center;
		margin-top: .2rem;
	}
</style>