<template>
	<div class="row">
		<div class="flexitem">
			<div class="item" v-for="(page, index) in pages">
				<span v-for="(item, index) in page" @click="deletequote(index)">{{item}}</span>
			</div>
		</div>
		
	</div>
</template>


<script>
	import {pubu} from '../../assets/js/pubu.js'
	import quote from './quote.vue'
	export default{
		data(){
			return{

			}
		},
		props:{
			quotes:{
				type:Array
			}
		},
		components:{
			quote,
		},
		methods:{
			deletequote(index){
				this.$emit('quoteDelete', index)
			}
		},
		computed:{
			pages(){
				const pages = []
				this.quotes.forEach((item, index) =>{
					const page = Math.floor(index / 10)
					if (!pages[page]) {
						pages[page] = []
					}
					pages[page].push(item)
				})
				return pages
			}
		},
		created(){
			/*this.quotes.forEach((item)=>{
				pubu(item)
			})*/
		}

	}
</script>

<style lang="scss" scoped>
	.row{
		width: 100%;
		padding:10% 4%;
		height: calc(100vh - .8rem);
		box-sizing: border-box;
		background: white;
		overflow: auto;
		.flexitem{
			display: flex;
			.item{
				width: 48%;
				height: 300px;
				&:first-child{
					margin-right: 4%;
				}
				span{
					padding:.2rem;
					box-sizing:border-box;
					display: block;
					height: auto;
					background:green;
					color: white;
					word-wrap: break-word;
					margin-bottom: .3rem;
					border-radius:.05rem;
					line-height: 150%;
					box-shadow:0 .06rem .04rem .02rem rgba(0,0,0,.2);
					font-size: .30rem;
					text-align: justify;
				}
			}
		}
		
	}
</style>
