<template>
	<div class="new" >
		<div class="newbg" @click="closenew()"></div>
		<div class="newbox">
			<textarea id="textarea" rows="3" cols="20" v-model="quote" autofocus="autofocus"></textarea>
			<div>
				<button @click="createNew()">添加名言</button>
			</div>
		</div>		
	</div>
</template>


<script>
	export default{
		data(){
			return{
				quote:''
			}
		},
		props:{
			maxQuotes:{
				type:Number
			}
		},
		methods:{
			createNew(){	
				if (this.maxQuotes >= 20) {
					alert('名言数量已满，要添加的话请尝试点击名言删除后再添加')
					this.quote = ""
					this.$emit('close');
					return
				}	
				if (document.getElementById('textarea').value == '') {
					alert('有时候无言或许就是最好的表达诶')
					this.$emit('close');
				}
				this.$emit('quoteAdd',this.quote);
				this.$emit('close');
				this.quote = ""
			},
			closenew(){
				this.$emit('close')
			},
		}
	}
</script>

<style lang="scss">
	.new{
		.newbg{
			width: 100%;
			height: 100vh;
			background:rgba(0,0,0,.5);
			position:absolute;
			z-index: 2;
		}
		.newbox{
			width: 60%;
			height: 3rem;
			border-radius: .1rem;
			background: white;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			z-index: 3;
			text-align: center;
			#textarea{
				width: 80%;
				margin-top: .4rem;
				text-align: justify;
				border:.02rem solid green;
				color: green;
				font-size: .30rem;
				padding:.1rem;
				box-sizing: border-box;
				background:white;
			}
			div{
				margin-top: .2rem;
				button{
					padding: .1rem .4rem;
					box-sizing: border-box;
					color: white;
					background:green;
				}
			}
		}
	}
</style>